import React from 'react';
import PropTypes from 'prop-types';
import { Box } from '@mui/material';
import usePhotoStyle from '../../styles/PhotoStyles.jsx';

const Photo = ({ size, src, theme }) => {
  const {
    root, rootDark, small, medium, large, photo,
  } = usePhotoStyle();
  let photoClass;
  const isLightTheme = theme === 'light';

  if (size === 'small') {
    if (isLightTheme) {
      photoClass = `${root} ${small}`;
    } else {
      photoClass = `${root} ${rootDark} ${small}`;
    }
  } else if (size === 'medium') {
    if (isLightTheme) {
      photoClass = `${root} ${medium}`;
    } else {
      photoClass = `${root} ${rootDark} ${medium}`;
    }
  } else if (isLightTheme) {
    photoClass = `${root} ${large}`;
  } else {
    photoClass = `${root} ${rootDark} ${large}`;
  }

  const styles = {
    borderRadius: 16,
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    overflow: 'hidden',
    backgroundImage: `url(${src})`,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    transition: 'transform .2s ease-out',
    '&:hover': {
      transform: 'scale(2)',
      zIndex: 100,
    },
    position: 'relative',
  };

  return (
    <Box className={photoClass}>
      <Box className={photo} style={styles} />
    </Box>
  );
};

Photo.propTypes = {
  size: PropTypes.string,
  src: PropTypes.string,
  theme: PropTypes.string,
};

export default Photo;
