import React from 'react';
import PropTypes from 'prop-types';
import { Box, Typography } from '@mui/material';

import useGalleryStyle from '../../styles/GalleryStyles.jsx';
import Photo from './Photo.jsx';

import photos from './photos';

const Gallery = ({ theme }) => {
  const { root, header, gallery } = useGalleryStyle();

  return (
    <Box className={root}>
      <Typography className={header}>
        Gallery
      </Typography>

      <Box className={gallery}>
        {photos.map((p, idx) => <Photo size={p.size} key={idx} src={p.photo} theme={theme} />)}
      </Box>

    </Box>
  );
};

Gallery.propTypes = {
  theme: PropTypes.string,
};

export default Gallery;
