import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  Box, Typography, Link,
} from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import useApplicationStyle from '../../styles/ApplicationStyles.jsx';

const Application = ({
  title = '', demos = [], link = '', description = '', stack = [], theme,
}) => {
  const {
    root,
    applicationContainer,
    applicationContainerDark,
    appTitle,
    appDescription,
    appStack,
    demoContainer,
    demo,
    descriptionContainer,
  } = useApplicationStyle();
  const showCarousel = demos.length >= 3;
  const [activeIdx, setActiveIdx] = useState(0);
  const [display, setDisplay] = useState(demos[0]);
  const applicationClass = theme === 'light' ? applicationContainer : `${applicationContainer} ${applicationContainerDark}`;

  const handleRight = () => {
    if (activeIdx === demos.length - 1) {
      setActiveIdx(0);
    } else {
      setActiveIdx(activeIdx + 1);
    }
  };

  const handleLeft = () => {
    if (activeIdx === 0) {
      setActiveIdx(demos.length - 1);
    } else {
      setActiveIdx(activeIdx - 1);
    }
  };

  useEffect(() => {
    if (showCarousel) {
      const ToDisplay = demos[activeIdx];
      setDisplay(ToDisplay);
    }
  }, [activeIdx, showCarousel]);

  return (
    <Box className={root}>
      <Box className={applicationClass}>
        <Box className={demoContainer}>
          {showCarousel && <ArrowLeftIcon onClick={handleLeft} style={{ cursor: 'pointer' }} />}
          {!showCarousel && demos.map(
            (img, idx) => <img key={`${title}-${idx}`} src={img} alt='' className={demo} />,
          )}
          {showCarousel && <img src={display} alt='' className={demo} />}
          {showCarousel && <ArrowRightIcon onClick={handleRight} style={{ cursor: 'pointer' }} />}
        </Box>
        <Box className={descriptionContainer}>
          <Typography className={appTitle}>{title}</Typography>
          <Typography className={appDescription}>{description}</Typography>
          <Typography className={appStack}>Tech Stack: {stack.join(', ')}</Typography>

          <Link href={link} target='_blank' color='inherit'>
            <GitHubIcon fontSize='large' />
          </Link>

        </Box>
      </Box>
    </Box>
  );
};

Application.propTypes = {
  title: PropTypes.string,
  demos: PropTypes.array,
  link: PropTypes.string,
  description: PropTypes.string,
  stack: PropTypes.array,
  theme: PropTypes.string,
};

export default Application;
